























 import Vue from "vue"
import { Component, Prop, Watch } from "vue-property-decorator";

@Component

export default class NumberPad extends Vue{
  @Prop(Number) readonly value!: number;
  output = this.value.toString()  //Money

  @Watch('value')
    onValueChange(value: number){
      this.output = value.toString()
    }
  

  inputContent(event: MouseEvent){   //
  const button = (event.target as HTMLButtonElement)  //targetevent.target
  const input = button.textContent as string;  //(!as string)
  if(this.output.length === 10){return}   //10
  if(this.output === '0'){  //output0
  // if(input === '0'){return} //0
  if('0123456789'.indexOf(input)>=0){  //1-9>0length0
      this.output = input   //00
    }else{
      this.output+=input
    }
    return
  }
  if(this.output.indexOf('.')>=0 && input === '.'){return} 
  //
  this.output+=input
  }
  remove(){
    if(this.output.length === 1){
      this.output ='0'
    }else{
    this.output = this.output.slice(0,-1)
    }
  }
  clear(){
      this.output ='0'
  }
  ok(){
   const number = parseFloat(this.output)
   this.$emit('update:value',number)
   this.$emit('submit',number)
  //  this.output = '0'
  }
}

